import React from 'react'
import anime from 'animejs'
import cx from 'classnames'
import PropTypes from 'prop-types'
import { thousandComma } from '../../lib/numberCore'
import './style.scss'

export default class AnimateNum extends React.Component {
  static propTypes = {
    value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired,
    duration: PropTypes.number.isRequired
  }

  static defaultProps = {
    duration: 2000,
    value: 12345678,
    prefix: '',
    suffix: '',
    style: {}
  }

  constructor (props) {
    super(props)
    this.state = {
      curNum: props.value
    }

    this.animateObj = {
      curNum: props.value
    }
  }

  componentDidUpdate (prevProps) {
    if (prevProps.value !== this.props.value) {
      this.animteChangeAcount()
    }
  }

  animteChangeAcount = () => {
    anime.remove(this.animateObj)
    let {value, duration} = this.props

    if (isNaN(value) || value === '' || duration <= Number.EPSILON) {
      this.setState({curNum: value})
      return
    }

    this.animateObj = { curNum: this.state.curNum }
    anime({
      targets: this.animateObj,
      curNum: value,
      round: 1,
      easing: 'linear',
      duration,
      // attention，在页面卡顿的时候，这个update可能会不调用
      update: () => {
        if (this.state.curNum === this.animateObj.curNum) return
        this.setState({ curNum: this.animateObj.curNum })
      },
      complete: () => {
        if (this.state.curNum === value) return
        this.setState({ curNum: value })
      }
    })
  }

  componentWillUnmount () {
    anime.remove(this.animateObj)
  }

  render () {
    const { props, state } = this
    const {className, style, prefix, prefixClassName, prefixStyle, suffix, suffixClassName, suffixStyle} = props
    return (
      <div className='dui-animate-num' style={style}>
        {
          prefix && <span className={prefixClassName} style={prefixStyle}>{prefix}</span>
        }
        <span className={className}>
          {
            thousandComma(state.curNum)
          }
        </span>
        {
          suffix && <span className={suffixClassName} style={suffixStyle}>{suffix}</span>
        }
      </div>
    )
  }
}